body {
  padding: 0;
  margin: 0 0;
  font-family: sans-serif;
}
.header{
	background: url("https://images.unsplash.com/photo-1517856713891-215e57a13c0d?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&w=1000&q=80");
	position: relative;
	width: 100%;
	height: 350px;
	color: black;
}

h1{
	text-align: center;
	font-size: 90px;
	font-weight: bold;
	padding: 150px 0 20px 0; 
}

h2{
	text-align: center;
	font-size: 25px;
	padding-bottom: 40px;
}
.navbar{
    display: inline-block;
    width: 100%;

}

.navbar li {
    display: flex;
    display: inline-block;
    text-align: center;
  }
  
 .itemLeft {
    width: 15%;
  }
  
  .itemCenter {
      width: 60%;
  }

.itemRight {

    width: 25%;
  } 

  .navbar {
    background-color: black;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  }
  
  li {
    color: white;
    padding: 15px 0;
    
  }
  
  body {
    font-size: 24px;
  }

  @media only screen and (max-width: 750px) {
    .itemRight {
      font-size: 2rem;
    }
  }
.wrapper {
	background: url("https://images.unsplash.com/photo-1517856713891-215e57a13c0d?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&w=1000&q=80");
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  padding: 25px 0 50px;
  justify-content: space-around;
  align-content: flex-start;
}
.card {
    width: 200px;
    height: 150px;
    margin: 15px 45px;
  }
